.contacts {
  background-color: #06071b;

  overflow: hidden;
}

.contacts__title {
  position: relative;
  z-index: 10;

  text-align: center;

  padding-top: 40px;

  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;

  @media screen and (min-width: 768px) {
    padding-top: 74px;

    font-size: 24px;
    line-height: 1.2;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 80px;
  }
}

.container--box {
  position: relative;

  text-align: center;
  display: flex;
  flex-direction: column;

  margin-top: 40px;
  padding-bottom: 40px;

  @media screen and (min-width: 768px) {
    margin-top: 38px;
    padding-bottom: 147px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 80px;
    padding-bottom: 139px;

    flex-direction: row-reverse;
  }
}

.form {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 40px;
  gap: 20px;
  justify-content: center;

  z-index: 10;
  @media screen and (max-width: 767px) {
    flex-direction: column;
  }
  @media screen and (min-width: 768px) {
    margin-top: 38px;
    justify-content: space-between;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 0;
  }
}

.form::before {
  content: '';
  position: absolute;

  width: 198px;
  height: 307px;

  background-color: rgba(125, 85, 255, 0.3);
  filter: blur(51.5px);
  transform: matrix(0.9, 0.48, -0.41, 0.89, 0, 0);

  top: 0%;
  right: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;

  @media screen and (min-width: 768px) {
    width: 333px;
    height: 542px;

    transform: matrix(0.11, 1.01, -0.96, 0.2, 0, 0);

    top: -20%;
    right: 47%;
  }

  @media screen and (min-width: 1280px) {
    width: 297px;
    height: 458px;

    transform: matrix(0.22, 1.02, -0.9, 0.34, 0, 0);

    top: -17%;
    right: 23%;
  }
}

.form::after {
  content: '';
  position: absolute;

  width: 172px;
  height: 300px;

  background-color: rgba(0, 197, 235, 0.3);
  filter: blur(51.5px);
  transform: matrix(0.74, -0.63, -0.73, -0.74, 0, 0);

  top: -6%;
  left: 55%;
  transform: translate(-50%, -50%);
  z-index: 4;

  @media screen and (min-width: 768px) {
    width: 291px;
    height: 528px;

    transform: matrix(-0.03, 1, 0.99, -0.09, 0, 0);

    top: -20%;
    left: 48%;
  }

  @media screen and (min-width: 1280px) {
    width: 230px;
    height: 443px;

    transform: matrix(0.06, 1.03, 0.94, -0.25, 0, 0);

    top: -18%;
    left: 70%;
  }
}

.form ::placeholder {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.21;

  color: #939eac;
}

.form__name {
  padding: 12px;

  border: none;
  border-radius: 5px;

  z-index: 10;

  &:valid {
    border: 2px solid black;
  }

  &:invalid {
    border: 2px solid red;
  }

  @media screen and (min-width: 768px) {
    width: 336px;
  }

  @media screen and (min-width: 1280px) {
    width: 288px;
  }
}

.form__mail {
  padding: 12px;

  border: none;
  border-radius: 5px;

  z-index: 10;

  &:valid {
    border: 2px solid black;
  }

  &:invalid {
    border: 2px solid red;
  }

  @media screen and (min-width: 768px) {
    width: 336px;
  }

  @media screen and (min-width: 1280px) {
    width: 288px;
  }
}

.form__message {
  resize: none;

  padding: 12px;

  height: 220px;

  border: none;
  border-radius: 5px;

  z-index: 10;

  @media screen and (min-width: 768px) {
    width: 704px;
    height: 162;
  }

  @media screen and (min-width: 1280px) {
    width: 608px;
  }
}

.form__button {
  position: relative;

  display: block;

  padding: 0;
  margin-left: auto;
  margin-right: auto;

  min-width: 181px;
  min-height: 42px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.19;
  text-align: center;

  color: var(--primary-color-of-text);

  border: none;
  border-radius: 5px;

  z-index: 10;

  overflow: hidden;
  cursor: pointer;

  &::after,
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
  }

  &::before {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(90deg, #00c5eb -2%, #b544fe 90%);
    background-size: cover;
    background-repeat: no-repeat;
  }

  &::after {
    width: calc(100% - 2px);
    height: calc(100% - 2px);
    border-radius: 5px;
    background-color: var(--secondary-background-color);
    opacity: 0;
    transition: 300ms;
  }

  &__text {
    position: relative;
    z-index: 10;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 0;

    &:hover::after,
    &:focus::after {
      opacity: 1;
    }
  }
}

.contacts--box {
  @media screen and (min-width: 1280px) {
    text-align: left;
    margin-right: 140px;
  }
}

.contacts--box::before {
  content: '';
  position: absolute;

  width: 180px;
  height: 35px;

  background-color: rgba(0, 197, 235, 0.4);
  filter: blur(51.5px);
  transform: rotate(-149.52deg);

  top: 88%;
  right: 31%;
  transform: translate(-50%, -50%);
  z-index: 5;

  @media screen and (min-width: 768px) {
    width: 213px;
    height: 42px;

    transform: rotate(-180deg);

    top: 47%;
    right: 44%;
  }

  @media screen and (min-width: 1280px) {
    background: rgba(0, 197, 235, 0.3);
    transform: matrix(-1, 0, 0, 1, 0, 0);

    top: -15%;
    right: 79%;
  }
}

.contacts--box::after {
  content: '';
  position: absolute;

  width: 110px;
  height: 35px;

  background-color: rgba(125, 85, 255, 0.4);
  filter: blur(51.5px);
  transform: rotate(-149.52deg);

  top: 98%;
  left: 51%;
  transform: translate(-50%, -50%);
  z-index: 6;

  @media screen and (min-width: 768px) {
    width: 130px;
    height: 42px;

    transform: rotate(-180deg);

    top: 54%;
    left: 50%;
  }

  @media screen and (min-width: 1280px) {
    background: rgba(125, 85, 255, 0.3);
    transform: rotate(-180deg);

    top: -23%;
    left: 17%;
  }
}

.contacts__title-mini {
  position: relative;
  z-index: 10;

  margin-bottom: 20px;

  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;

  @media screen and (min-width: 768px) {
    font-size: 24px;
  }
}

.contacts__text {
  position: relative;
  z-index: 10;

  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.21;

  width: 280px;
  height: 51px;

  @media screen and (min-width: 768px) {
    width: 500px;
    height: 38;

    font-size: 16px;
    line-height: 1.18;
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 20px;
  }
}

.contacts_social {
  display: flex;
  justify-content: center;

  margin: 0;
  margin-bottom: 40px;
  padding: 0;

  @media screen and (min-width: 1280px) {
    justify-content: left;
    margin-bottom: 20px;
  }
}

.contacts__list:not(:first-child) {
  margin-left: 14px;
}

.social-icon {
  border-radius: 50%;

  background-image: var(--buttons-gradient);

  @media screen and (min-width: 1280px) {
    justify-content: left;
  }
}

.social-icon__main {
  transition: 400ms;

  @media screen and (min-width: 1280px) {
    .socials-link:hover .social-icon & {
      opacity: 0;
    }
  }
}

.social-icon__hover {
  opacity: 0;
  transition: 400ms;

  @media screen and (min-width: 1280px) {
    .socials-link:hover .social-icon & {
      opacity: 1;
    }
  }
}

.contacts__adress {
  display: flex;
  flex-direction: column;
}

.contacts__link {
  text-decoration: none;
  font-style: normal;

  color: var(--primary-color-of-text);

  &:not(:last-child) {
    margin-bottom: 12px;
  }
}

.contacts--phone {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.19;
}

.contacts--mail {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.21;
}

.footer {
  z-index: 10;

  border-top: 1px solid var(--primary-color-of-text);

  background-color: #06071b;
}

.footer__text {
  padding-top: 22px;
  padding-bottom: 22px;

  font-weight: 500;
  font-size: 14px;
  line-height: 1.21;
}

.container--footer {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  br {
    display: none;
  }
}
